import myPhoto from "./assets/myPhoto.png"
import React from "react";
const About = () => {
    return (
        <div style={divStyle}>
            <img src={myPhoto} alt="" style={photoStyle} />
            <p style={paragraphStyle}>My name is Kenan Aljaber I'm from Damascus Syria, I started learning programing
                and software deveopment 4 years ago<br/> This is my first time using a front-end technology so hope it is not that bad.</p>
        </div>
        
        );

}

const divStyle= {
    textAlign: "center",
    padding:"20px"
}

const photoStyle = {
    width: "20%",
    borderRadius: "10px",
    

}
const paragraphStyle = {
  
    verticalAlign: "top",
    marginLeft: "8px",
    fontSize: "18px",
    color: "#058CFF",
    fontWeight:"bold"

}
export default About;
