import React from "react";
import defaultImage from "../assets/default-pokemon.png"

const ImageHolder = ({ imageSrc, imageAlt}) => {
    return (
        <div>
            <img className="listImageClass" src={imageSrc} alt={imageAlt} />
           


           
        </div>
        
        )

}

ImageHolder.defaultProps = {
    imageSrc: defaultImage,
    imageAlt:"Pokemon"
}

export default ImageHolder; 