import SearchWidget from "./SerachWidget"
import Button from "./Button"
import PokemonHolder from "./PokemonHolder";
import React, { useEffect, useState } from "react";
import axios from "axios";



const HomeBody = () => {
    const pokemonsApi = "http://localhost:8080/api-kruger/pokemon" + "/all";
   
    
    //declaring the list that will be updated and filled with the data of the api
    const [pokemons, setPokemons] = useState([]);
    //the pokemon name that user will look for 
    const [pokemonToSearch, setPokemonToSearch] = useState("");
    /*
    @btntitle a string used to switch the button title between Buscar/Cancelar
    @btnColor a string represents the hash value of the btn color 
    @searchedClicked a boolean flag to indicate us when the user click Buscar button 
    */
    const [btnTitle, setBtnTitle] = useState("Buscar");
    const [btnColor, setBtnColor] = useState("#058CFF");
    const [searchedClicked, setSearchedClicked] = useState(false);
     //an object that will hold the pokemon that user is loking for
     let searchedItem = [];
    	
    //fetching the api data
    useEffect(() => {
        //if the user did not searched for any results yet then the page is recently loaded so 
        //get the data from the api
        if (!searchedClicked) {
            
            axios.get(pokemonsApi).then((response) => {
                console.log(response.data);
            setPokemons(response.data);
            });
        }
    }, [setPokemons,])

    function search() {
        
        //once the user clicked Buscar button set the setSearchedClicked to the opisite
        setSearchedClicked(!searchedClicked);
        if (!searchedClicked) {
            //check wheather the pokemon name exits in the list
            pokemons.forEach(it => {
                
                if (it.name.toLowerCase() === pokemonToSearch.toLowerCase()) {
                    
                    // console.log(pokemonToSearch + " Found");
                    searchedItem[0] = it;
                    console.log(it);
                }
            });
            //pokemon name not found
            if (searchedItem == null) {
                console.log("clicked");
                setPokemons(pokemons);
            } 
            //found update pokemons list, change buscar button color and text also set setSearchedClicked to true
            else
             {
                console.log("clicked true");
                console.log(searchedItem.name);
                const onePokemonList = [searchedItem];
                setPokemons(searchedItem);
                setBtnTitle("Cancelar");
                setBtnColor("#CD5C5C");
                setSearchedClicked(true);
            }
        } 
        // if the user already searched we get back all pokemons and change the btn theme
        else {
            setSearchedClicked(false);
            setBtnTitle("Buscar");
            setPokemons(pokemons);
            setBtnColor("#058CFF");
        }
    }



    const onchange = e => {
        setPokemonToSearch(e.target.value);
    }



    //this counter used to give an uniqe id to each list element
    let counter = 0;
    return (
        <div>
            <div className="searchContainer">
                <SearchWidget searchHint="Busca tu pokemon favorito..." onchange={onchange} />
                <Button title={btnTitle} onClick={search} btnHref="#" btnColor={btnColor} /><br />
            </div>
            <div className="grid">
                {
                   
                    pokemons.slice(0,10).map(currentItem => {
                        return (
                           
                            <div className="item" key={counter++}>
                                
                                <PokemonHolder btnTitle="Detalles" pokemonName={currentItem.name}
                                 disc={currentItem.description} textOnImage={currentItem.name}
                                  btnHref={'/pokemon/' + currentItem.id} imageSrc={currentItem.photo} />
                                
                            </div>
                        
                            
                        
                        );
                    })};
                  
                  <Button  className="nextBtn" title="Next" onClick={()=> {
                  
                    setSearchedClicked(true)
                    
                    
                }
                }/>
            </div>

        </div>
    )
}

export default HomeBody;